// Operations.
import remoteStart from './start'
import remoteFinish from './finish'

import { OPERATIONS } from '../../../interface/constants'

export default function initOperation(bottle) {
  // Remote-only "slow start" operation.
  bottle.factory('Callstack.operations.slowStart', () => {
    const opFactory = bottle.container.Callstack.models.Operation

    /**
     * Factory function for a RemoteHold operation.
     * @method RemoteHold
     * @param {string} callId ID for the call this operation will affect.
     * @return {Operation} An instance of a remote hold operation.
     */
    function RemoteHold(callId) {
      return opFactory.instance(
        {
          // Operation meta-data.
          type: OPERATIONS.SLOW_START,
          isNegotiation: true,
          isLocal: false,
          // Operation methods.
          stages: {
            remoteOffer: remoteStart(bottle.container),
            remoteAnswer: remoteFinish(bottle.container)
          }
        },
        {
          callId
        }
      )
    }

    return {
      remote: RemoteHold
    }
  })
}
