// Operations.
import localAnswer from './local/answer'
import validate from './local/validate'

import slowAnswer from './slow/slowAnswer'
import slowValidate from './slow/validate'
import remoteSlowAnswer from './slow/remoteSlowAnswer'

// Helpers
import answerWebrtcSession from './answerWebrtcSession'

import { OPERATIONS } from '../../../interface/constants'

export default function initOperation(bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.answer', () => {
    const opFactory = bottle.container.Callstack.models.Operation

    /**
     * Factory function for a LocalAnswer operation.
     * @method LocalAnswer
     * @param {string} callId ID for the call this operation will affect.
     * @return {Operation} An instance of a local answer operation.
     */
    function LocalAnswer(callId) {
      return opFactory.instance(
        {
          type: OPERATIONS.ANSWER,
          isNegotiation: false,
          isLocal: true,
          stages: {
            validate,
            // TODO: This is `localOffer` because the "localOperation" flow uses that
            //    function. Eventually this should be "fixed" to properly reflect the answer.
            localOffer: localAnswer(bottle.container)
          }
        },
        {
          callId
        }
      )
    }

    return {
      local: LocalAnswer
    }
  })

  bottle.factory('Callstack.operations.answerSlow', () => {
    const opFactory = bottle.container.Callstack.models.Operation

    /**
     * Factory function for a SlowAnswer operation.
     * @method SlowAnswer
     * @param {string} callId ID for the call this operation will affect.
     * @return {Operation} An instance of a local slow-start answer operation.
     */
    function SlowAnswer(callId) {
      return opFactory.instance(
        {
          type: OPERATIONS.ANSWER,
          isNegotiation: true,
          isLocal: true,
          stages: {
            validate: slowValidate,
            localOffer: slowAnswer(bottle.container),
            remoteAnswer: remoteSlowAnswer(bottle.container)
          }
        },
        {
          callId
        }
      )
    }

    return {
      local: SlowAnswer
    }
  })

  bottle.factory('Callstack.utils.answerWebrtcSession', () => answerWebrtcSession(bottle.container))
}
