// Operations.
import holdOperation from './local/hold'
import validate from './local/validate'
import rollbackOperation from './rollbackHold'
import remoteAnswer from './local/remoteAnswer'

import remoteOffer from './remote/remoteOffer'

import { OPERATIONS } from '../../../interface/constants'

export default function initOperation(bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.hold', () => {
    const opFactory = bottle.container.Callstack.models.Operation

    function hold(isLocal, stages) {
      /**
       * Factory function for a Hold operation.
       * @method hold
       * @param {string} callId ID for the call this operation will affect.
       * @return {Operation} An instance of the hold operation.
       */
      return callId => {
        return opFactory.instance(
          {
            // Operation meta-data.
            type: OPERATIONS.HOLD,
            isNegotiation: true,
            isLocal,
            // Operation methods.
            stages
          },
          {
            callId
          }
        )
      }
    }

    return {
      local: hold(true, {
        // Stages of local operation.
        validate,
        localOffer: holdOperation(bottle.container),
        remoteAnswer: remoteAnswer(bottle.container)
      }),
      remote: hold(false, {
        // Stages of remote operation.
        remoteOffer: remoteOffer(bottle.container)
      })
    }
  })

  bottle.factory('Callstack.utils.rollbackHold', () => rollbackOperation(bottle.container))
}
