// Operations.
import addMediaOperation from './local/addMedia'
import addMediaValidate from './local/validate'
import webrtcAddMediaOperation from './webrtcAddMedia'
import rollbackOperation from './rollbackAddMedia'
import basicValidate from './local/validateBasic'

import remoteOffer from './remote/remoteOffer'
import remoteAnswer from './local/remoteAnswer'

import { OPERATIONS } from '../../../interface/constants'

export default function initOperation(bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.addMedia', () => {
    const opFactory = bottle.container.Callstack.models.Operation

    function addMedia(isLocal, stages) {
      /**
       * Factory function for AddMedia operation.
       * @method addMedia
       * @param {string} callId ID for the call this operation will affect.
       * @return {Operation} An instance of an addMedia operation.
       */
      return callId => {
        return opFactory.instance(
          {
            // Operation meta-data.
            type: OPERATIONS.ADD_MEDIA,
            isNegotiation: true,
            isLocal,
            // Operation methods.
            stages
          },
          {
            callId
          }
        )
      }
    }

    return {
      local: addMedia(true, {
        // Stages of local operation.
        validate: addMediaValidate,
        localOffer: addMediaOperation(bottle.container),
        remoteAnswer: remoteAnswer(bottle.container)
      }),
      remote: addMedia(false, {
        // Stages of remote operation.
        remoteOffer: remoteOffer(bottle.container)
      })
    }
  })

  bottle.factory('Callstack.operations.addBasicMedia', () => {
    const opFactory = bottle.container.Callstack.models.Operation

    function basicAddMedia(isLocal, stages) {
      /**
       * Factory function for basic AddMedia operation.
       * @method basicAddMedia
       * @param {string} callId ID for the call this operation will affect.
       * @return {Operation} An instance of the basic addMedia operation.
       */
      return callId => {
        return opFactory.instance(
          {
            // Operation meta-data.
            type: OPERATIONS.ADD_BASIC_MEDIA,
            isNegotiation: true,
            isLocal,
            // Operation methods.
            stages
          },
          {
            callId
          }
        )
      }
    }

    return {
      local: basicAddMedia(true, {
        // Stages of local operation.
        validate: basicValidate,
        localOffer: addMediaOperation(bottle.container),
        remoteAnswer: remoteAnswer(bottle.container)
      })
      // There is no remote 'basic' operation; it is seen as a regular removeMedia operation.
    }
  })

  // Helpers used only within the operation.
  bottle.factory('Callstack.utils.webrtcAddMedia', () => webrtcAddMediaOperation(bottle.container))
  bottle.factory('Callstack.utils.rollbackAddMedia', () => rollbackOperation(bottle.container))
}
